'use client';

import React, { useState, useEffect, useMemo, useCallback } from 'react';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { MapComponent } from '@/components/map';
import { getIncidents } from '@/lib/incidents-service';
import { Incident } from '@/lib/types';
import { useToast } from '@/hooks/use-toast';
import { LoaderCircle, MapPin, Bot, Volume2, Route, Flag, Pin } from 'lucide-react';
import { generateRouteAlert, GenerateRouteAlertOutput } from '@/ai/flows/generate-route-alert-flow';
import { textToSpeech } from '@/ai/flows/text-to-speech-flow';
import { getDirections } from '@/lib/directions-service';
import { Badge } from '@/components/ui/badge';
import { ToggleGroup, ToggleGroupItem } from '@/components/ui/toggle-group';
import { getPlaceDetails } from '@/lib/places-service';

type Location = { lat: number; lng: number, address?: string };
type AlertResult = GenerateRouteAlertOutput['detailedAlerts'][0];

export default function RouteAlertsPage() {
  const [allIncidents, setAllIncidents] = useState<Incident[]>([]);
  const [startLocation, setStartLocation] = useState<Location | null>(null);
  const [endLocation, setEndLocation] = useState<Location | null>(null);
  const [selectionMode, setSelectionMode] = useState<'start' | 'end'>('start');
  const [isAnalyzing, setIsAnalyzing] = useState(false);
  
  const [routePolyline, setRoutePolyline] = useState<string | undefined>(undefined);
  const [alertSummary, setAlertSummary] = useState<string | null>(null);
  const [alertDetails, setAlertDetails] = useState<AlertResult[]>([]);
  const [audioDataUri, setAudioDataUri] = useState<string | null>(null);

  const { toast } = useToast();

  useEffect(() => {
    const fetchIncidents = async () => {
      const result = await getIncidents();
      if (result.success && result.data) {
        setAllIncidents(result.data);
      } else {
        toast({
          variant: 'destructive',
          title: 'Failed to load incidents',
          description: result.error || 'Could not fetch incidents from the server.',
        });
      }
    };
    fetchIncidents();
  }, [toast]);
  
  const mapCenter = useMemo(() => {
    // Center the map between start and end if both are set
    if (startLocation && endLocation) {
      return {
        lat: (startLocation.lat + endLocation.lat) / 2,
        lng: (startLocation.lng + endLocation.lng) / 2,
      };
    }
    // Otherwise, center on the first point that is set
    return startLocation || endLocation || { lat: 37.7749, lng: -122.4194 };
  }, [startLocation, endLocation]);

  const mapZoom = useMemo(() => {
     if (startLocation && endLocation) return 12;
     return 10;
  }, [startLocation, endLocation])

  const mapPins = useMemo(() => {
    const pins: Incident[] = [];
    if(startLocation) {
        pins.push({ id: 'start', location: { latitude: startLocation.lat, longitude: startLocation.lng }, category: 'Home', severity: 'Low', summary: 'Start', timestamp: ''})
    }
    if(endLocation) {
        pins.push({ id: 'end', location: { latitude: endLocation.lat, longitude: endLocation.lng }, category: 'School', severity: 'Low', summary: "End", timestamp: ''})
    }
    return pins;
  }, [startLocation, endLocation])

  const handleMapClick = async (e: google.maps.MapMouseEvent) => {
    if (e.latLng) {
      const location = {
        lat: e.latLng.lat(),
        lng: e.latLng.lng(),
      };
      
      const geocoder = new google.maps.Geocoder();
      const response = await geocoder.geocode({ location });
      const address = response.results[0]?.formatted_address || `${location.lat.toFixed(5)}, ${location.lng.toFixed(5)}`;

      if (selectionMode === 'start') {
        setStartLocation({...location, address});
        setSelectionMode('end'); // Automatically switch to selecting the end point
      } else {
        setEndLocation({...location, address});
      }
    }
  };
  
  const resetAnalysis = () => {
      setAlertSummary(null);
      setAlertDetails([]);
      setAudioDataUri(null);
      setRoutePolyline(undefined);
  }
  
  const isIncidentOnRoute = (incident: Incident, bounds: google.maps.LatLngBounds, polyline: google.maps.LatLng[]) => {
      if (!bounds.contains({ lat: incident.location.latitude, lng: incident.location.longitude })) {
          return false;
      }
      const incidentLatLng = new google.maps.LatLng(incident.location.latitude, incident.location.longitude);
      return google.maps.geometry.polyUtil.isLocationOnEdge(incidentLatLng, new google.maps.Polyline({ path: polyline }), 1e-3);
  }

  const handleAnalyzeRoute = async () => {
    if (!startLocation || !endLocation) {
      toast({ variant: "destructive", title: "Please select start and end locations." });
      return;
    }
    
    if (typeof window === 'undefined' || !window.google || !window.google.maps.geometry || !window.google.maps.geometry.encoding || !window.google.maps.geometry.polyUtil) {
        toast({ variant: 'destructive', title: 'Map service not available', description: 'Google Maps libraries are not loaded yet. Please try again in a moment.' });
        return;
    }

    setIsAnalyzing(true);
    resetAnalysis();
    
    try {
      const directionsResult = await getDirections(startLocation, endLocation);
      if (!directionsResult.success || !directionsResult.data) {
        throw new Error(directionsResult.error || 'Could not get directions.');
      }
      const route = directionsResult.data;
      setRoutePolyline(route.polyline);

      const bounds = new google.maps.LatLngBounds(
          { lat: route.bounds.southwest.lat, lng: route.bounds.southwest.lng },
          { lat: route.bounds.northeast.lat, lng: route.bounds.northeast.lng }
      );
      const decodedPath = google.maps.geometry.encoding.decodePath(route.polyline);
      const incidentsOnRoute = allIncidents.filter(incident => isIncidentOnRoute(incident, bounds, decodedPath));

      if (incidentsOnRoute.length === 0) {
        setAlertSummary('No incidents found on your route. Drive safely!');
        return;
      }

      const alertResult = await generateRouteAlert({
        incidents: incidentsOnRoute,
        fromAddress: startLocation.address || 'start point',
        toAddress: endLocation.address || 'end point',
      });

      setAlertSummary(alertResult.alertSummary);
      setAlertDetails(alertResult.detailedAlerts);
      
      const ttsResult = await textToSpeech(alertResult.alertSummary);
      setAudioDataUri(ttsResult.audioDataUri);

    } catch (e: any) {
        console.error("Error analyzing route: ", e);
        toast({ variant: "destructive", title: "Failed to Analyze Route", description: e.message });
    } finally {
        setIsAnalyzing(false);
    }
  };

  const playAudio = () => {
      if (audioDataUri) {
          const audio = new Audio(audioDataUri);
          audio.play();
      }
  }

  return (
    <div className="bg-background min-h-screen">
      <main className="container mx-auto px-4 py-8 md:py-16">
        <header className="text-center mb-8">
          <h1 className="text-4xl sm:text-5xl font-bold font-headline text-primary flex items-center justify-center gap-3">
            <Route className="w-12 h-12" /> Route Alerts
          </h1>
          <p className="mt-4 text-lg text-muted-foreground">
            Click on the map to select your start and end points, then get AI-powered alerts for your route.
          </p>
        </header>

        <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
            <div className="md:col-span-1 space-y-6">
                <Card>
                    <CardHeader>
                        <CardTitle>Selection Mode</CardTitle>
                        <CardDescription>Choose which point to set on the map.</CardDescription>
                    </CardHeader>
                    <CardContent>
                        <ToggleGroup type="single" value={selectionMode} onValueChange={(value) => { if (value) setSelectionMode(value as 'start' | 'end')}} className="w-full">
                            <ToggleGroupItem value="start" className="w-1/2">
                                <Flag className="w-4 h-4 mr-2" /> Start
                            </ToggleGroupItem>
                            <ToggleGroupItem value="end" className="w-1/2">
                                <Pin className="w-4 h-4 mr-2" /> Destination
                            </ToggleGroupItem>
                        </ToggleGroup>
                    </CardContent>
                </Card>

                <Card>
                    <CardHeader>
                        <CardTitle>Selected Locations</CardTitle>
                    </CardHeader>
                    <CardContent className="space-y-4">
                        <div className="flex items-start gap-3">
                            <Flag className="w-5 h-5 text-green-500 mt-1" />
                            <div>
                                <p className="font-semibold">Start Point</p>
                                <p className="text-sm text-muted-foreground">{startLocation?.address || "Click map to select"}</p>
                            </div>
                        </div>
                         <div className="flex items-start gap-3">
                            <Pin className="w-5 h-5 text-red-500 mt-1" />
                            <div>
                                <p className="font-semibold">Destination</p>
                                <p className="text-sm text-muted-foreground">{endLocation?.address || "Click map to select"}</p>
                            </div>
                        </div>
                    </CardContent>
                </Card>

                <Button onClick={handleAnalyzeRoute} disabled={isAnalyzing || !startLocation || !endLocation} className="w-full text-lg py-6">
                    {isAnalyzing ? <><LoaderCircle className="mr-2 h-5 w-5 animate-spin" />Analyzing...</> : "Analyze Route"}
                </Button>
            </div>
          <div className="md:col-span-2 space-y-6">
            <Card className="h-[450px]">
              <MapComponent
                incidents={mapPins}
                onPinClick={() => {}}
                onMapClick={handleMapClick}
                center={mapCenter}
                zoom={mapZoom}
                routePolyline={routePolyline}
              />
            </Card>
            
            {alertSummary && (
                <Card className="animate-in fade-in-50">
                    <CardHeader>
                        <CardTitle className="flex items-center justify-between text-lg">
                            <div className='flex items-center gap-2'><Bot className="w-5 h-5 text-primary" />AI Summary</div>
                             {audioDataUri && (
                                <Button variant="ghost" size="icon" onClick={playAudio}>
                                    <Volume2 className="w-5 h-5" />
                                </Button>
                            )}
                        </CardTitle>
                    </CardHeader>
                    <CardContent>
                        <p className="text-muted-foreground">{alertSummary}</p>
                    </CardContent>
                </Card>
            )}

            {alertDetails.length > 0 && (
                <div className='mt-4 space-y-3'>
                    <h3 className='font-semibold'>Incidents on Route ({alertDetails.length})</h3>
                    {alertDetails.map(alert => (
                         <Card key={alert.incidentId} className='bg-muted/50'>
                            <CardContent className='pt-4'>
                                 <div className='flex justify-between items-start'>
                                    <p className="font-semibold text-base">{alert.category}</p>
                                    <Badge variant="outline">{alert.severity} Severity</Badge>
                                 </div>
                                 <p className="text-sm text-muted-foreground mt-1">{alert.summary}</p>
                            </CardContent>
                         </Card>
                    ))}
                </div>
            )}
          </div>
        </div>
      </main>
    </div>
  );
}
